<template>
  <div id="display-container">
    <div class="header">
      <div class="number-of-count">
        <span class="number-of-question">
          {{ arrIdxUsed.length }}/{{ questions.length }}
        </span>
      </div>
      <div class="control-container">
        <div
          v-if="!isMultipleQuiz"
          class="control-quiz"
          @click="redirectQuiz('back')"
        >
          <i class="fa fa-angle-double-left" aria-hidden="true"></i>
        </div>
        <div class="title-quiz">
          <a href="#demo-modal">{{ title }}</a>
        </div>
        <div
          v-if="!isMultipleQuiz"
          class="control-quiz"
          @click="redirectQuiz('next')"
        >
          <i class="fa fa-angle-double-right" aria-hidden="true"></i>
        </div>
      </div>
      <div style="display: flex; flex-direction: column; align-items: center">
        <label class="switch">
          <input v-model="showHira" type="checkbox" />
          <span class="slider round"></span>
        </label>
        Furigana
      </div>
    </div>
    <div id="container">
      <div v-if="typeTest === 1" class="question">
        <h1>
          <span v-if="showHira" v-html="furiganaRender()"></span>
          <span v-else>{{ questionSelect.text_kanji }}</span>
          <label
            href="#"
            style="font-size: 23px; margin-left: 10px; cursor: pointer"
            @click="textToSpeech(questionSelect)"
          >
            <i class="fa fa-volume-up" aria-hidden="true"></i>
          </label>
        </h1>
      </div>
      <div v-if="typeTest === 0" class="question">
        <h1>
          {{ questionSelect.text_vi }}
          <label
            href="#"
            style="font-size: 23px; margin-left: 10px; cursor: pointer"
            @click="textToSpeech(questionSelect)"
          >
            <i class="fa fa-volume-up" aria-hidden="true"></i>
          </label>
        </h1>
      </div>
      <input
        v-model="answer"
        type="text"
        class="option-div"
        :class="{ 'is-invalid': !isTrue }"
        placeholder="Answer"
        @keyup.enter="checkAnswer()"
        @focus="toggleAnswerInput(true)"
        @blur="toggleAnswerInput(false)"
      />
    </div>
    <div class="button-control">
      <button id="check-button" @click="checkFunc()">
        Switch to {{ typeTest === 1 ? "vi" : "ja" }}
      </button>
      <button id="next-button" @click="next()">Next</button>
    </div>
  </div>
  <div id="demo-modal" class="modal">
    <div class="modal__content">
      <div class="title-modal">
        <label>{{ title }}</label>
      </div>
      <div class="example" style="overflow-y: auto; height: 85%">
        <div class="data-render-container">
          <table>
            <thead>
              <tr>
                <th
                  scope="col"
                  class="cpointer"
                  style="width: 50px"
                  @click="toggleAll()"
                >
                  #
                </th>
                <th scope="col" class="cpointer" @click="hideTextJP()">Ja</th>
                <th scope="col">Play</th>
                <th scope="col">Ja</th>
              </tr>
            </thead>
            <tbody>
              <RowComponent
                v-for="(question, index) in listQuestionUse"
                :key="index"
                :question="question"
                :no-index="index"
                :update-question="
                  (data, noIndex) => updateQuestion(data, noIndex)
                "
                :loaded="loaded"
                :toggle-question="(noIndex) => toggleQuestion(noIndex)"
                :toggle-all="() => toggleAll()"
                :text-to-speech="(question) => textToSpeech(question)"
                :hide-text="hideText"
                type-table="quiz"
              />
            </tbody>
          </table>
        </div>
        <div class="add-container">
          <a
            v-if="!isMultipleQuiz"
            type="button"
            class="button-example add-button"
            @click="addQuiz()"
          >
            Add
          </a>
        </div>
      </div>
      <div class="modal__footer">
        <a
          v-if="!isMultipleQuiz"
          style="margin-left: 15px"
          type="button"
          class="button-example"
          @click="updateQuiz()"
        >
          Save
        </a>
        <a
          v-if="!isMultipleQuiz"
          type="button"
          class="button-example close-button"
          @click="close()"
        >
          Close
        </a>
      </div>
      <a href="#" class="modal__close">&times;</a>
    </div>
  </div>
</template>

<script>
import FirestoreService from "@/services/firestore";
import { fit } from "furigana";
import RowComponent from "@/components/RowComponent.vue";

export default {
  components: {
    RowComponent,
  },
  data() {
    return {
      winStrick: 0,
      title: "",
      questions: [],
      listQuestionUse: [],
      answer: "",
      questionSelect: { question: "" },
      typeTest: 0,
      isTrue: true,
      typeSelect: 0,
      arrIdxUsed: [],
      showHira: true,
      elm: null,
      funcHandle: null,
      listEdit: [],
      focusAnswer: false,
      isMultipleQuiz: false,
      typeQuiz: 1,
      createdAt: null,
      loaded: false,
      lastQuestion: null,
      quizs: [],
      indexQuizCurrent: 0,
      hideText: false,
    };
  },
  watch: {
    showHira(newVal) {
      localStorage.setItem("showHira", newVal);
    },
    typeSelect(newVal) {
      localStorage.setItem("typeSelect", newVal);
    },
    typeTest(newVal) {
      localStorage.setItem("typeTest", newVal);
    },
  },
  created() {
    const clickTimer = null;
    this.loadSetting();
    this.initData();
    this.elm = window;
    this.funcHandle = (event) => {
      if (event.keyCode === 37 && !this.focusAnswer) {
        this.showHira = !this.showHira;
      }
      if (event.keyCode === 38) {
        this.checkFunc();
      }
      if (event.keyCode === 39 && !this.focusAnswer) {
        this.next();
      }
      if (event.keyCode === 40) {
        this.textToSpeech(this.questionSelect);
      }
    };

    this.elm.removeEventListener("keydown", this.funcHandle);
    this.elm.addEventListener("keydown", this.funcHandle);
  },
  unmounted() {
    this.elm.removeEventListener("keydown", this.funcHandle);
  },
  methods: {
    async initData() {
      this.questions = [];
      this.title = "";
      this.arrIdxUsed = [];

      const listId = this.$route.params.id.split("_");
      this.isMultipleQuiz = listId.length > 1;

      if (!this.isMultipleQuiz) {
        if (this.$route.name === "KotobaQuizPage") {
          this.quizs = await FirestoreService.getAll("quizs", this.typeQuiz);
        } else {
          this.quizs = await FirestoreService.getAll("quizs", 2);
        }

        this.indexQuizCurrent = this.quizs.findIndex((q) => q.id === listId[0]);
      }

      const proms = [];
      listId.map((id) => proms.push(FirestoreService.get("quizs", id)));
      const d = await Promise.all(proms);
      d.map((item) => {
        item.questions = item.questions.map((question) => {
          let furigana = "";
          try {
            furigana = fit(
              question.text_kanji.replace("～", ""),
              question.text_jp.replace("～", ""),
              { type: "object" }
            );
          } catch (error) {
            furigana = null;
          }

          return {
            ...question,
            furigana: furigana,
          };
        });

        this.questions = [...this.questions, ...item.questions];
        this.title += ` ${item.title}`;
        this.typeQuiz = item.type;
        this.createdAt = item.created_at;
      });
      this.listQuestionUse = this.questions.map((q) => ({
        ...q,
        select: true,
      }));
      this.questionSelect = this.get_random(this.questions);
      this.listEdit = this.listQuestionUse.map((item) => {
        return { edit: 0 };
      });

      setTimeout(() => {
        this.loaded = true;
      }, 300);
    },
    get_random(list) {
      if (this.typeSelect === "") {
        this.typeTest = this.getRandomInt(2);
      }
      if (this.arrIdxUsed.length === this.questions.length) {
        this.arrIdxUsed = [];
      }
      const idx = Math.floor(Math.random() * list.length);
      if (!this.arrIdxUsed.includes(idx) && this.lastQuestion !== idx) {
        this.arrIdxUsed.push(idx);
        this.lastQuestion = idx;

        return list[idx];
      }

      return this.get_random(list);
    },
    next() {
      this.questionSelect = this.get_random(this.questions);
      this.answer = "";
    },
    checkAnswer() {
      let checked = false;
      if (this.typeTest === 1) {
        checked = this.answer.toLowerCase() === this.questionSelect.text_jp;
      }
      if (this.typeTest === 0) {
        checked = this.answer.toLowerCase() === this.questionSelect.text_jp;
        if (!checked) {
          checked =
            this.answer.toLowerCase() === this.questionSelect.text_kanji;
        }
      }
      if (checked) {
        this.textToSpeech(this.questionSelect);
        this.isTrue = true;
        this.questionSelect = this.get_random(this.questions);
        this.answer = "";
        this.winStrick++;
      } else {
        this.winStrick = 0;
        this.isTrue = false;
      }
    },
    getRandomInt(max) {
      return Math.floor(Math.random() * max);
    },
    selectType(e) {
      if (e.target.checked) {
        this.typeTest = 1;
      } else {
        this.typeTest = 0;
      }
    },
    checkFunc() {
      if (this.typeSelect == 1) {
        this.typeTest = 0;
        this.typeSelect = 0;
      } else if (this.typeSelect == 0) {
        this.typeTest = 1;
        this.typeSelect = 1;
      }
    },
    toggleQuestion(index) {
      this.saveListQuestionUse();
    },
    saveListQuestionUse() {
      const listItemUse = this.listQuestionUse.filter(
        (item) => item.select === true
      );
      if (listItemUse.length) {
        this.questions = listItemUse;
        this.arrIdxUsed = [];
        // document.querySelector(".modal__close").click();
      } else {
        // alert("Can't Save List Question!!!");
      }
    },
    updateQuiz() {
      this.listEdit = this.listQuestionUse.map((item) => {
        return { edit: 0 };
      });
      const data = this.listQuestionUse.map((item) => ({
        text_jp: item.text_jp,
        text_kanji: item.text_kanji,
        text_vi: item.text_vi,
        romanji: item.romanji,
      }));
      const dataUpload = {
        created_at: this.createdAt,
        title: this.title.trim(),
        del_flag: false,
        questions: data,
        type: this.typeQuiz,
      };
      FirestoreService.set("quizs", this.$route.params.id, dataUpload).then(
        () => {
          // document.querySelector(".modal__close").click();
          alert("Updated");
        }
      );
    },
    close() {
      document.querySelector(".modal__close").click();
    },
    toggleAll() {
      const itemChecked = this.listQuestionUse.find(
        (item) => item.select === false
      );
      if (itemChecked) {
        this.listQuestionUse = this.listQuestionUse.map((q) => {
          return { ...q, select: true };
        });
      } else {
        this.listQuestionUse = this.listQuestionUse.map((q) => {
          return { ...q, select: false };
        });
      }
      this.saveListQuestionUse();
    },
    textToSpeech(question) {
      let text = question.text_jp;
      if (text.length === 0) {
        text = question.text_kanji;
      }
      const utterance = new SpeechSynthesisUtterance();
      utterance.text = text;
      utterance.lang = "ja-JP";
      speechSynthesis.speak(utterance);
    },
    editMode(index) {
      this.listEdit[index].edit = !this.listEdit[index].edit;
    },
    toggleAnswerInput(value) {
      this.focusAnswer = value;
    },
    isMobile() {
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        return true;
      }

      return false;
    },
    addQuiz() {
      this.listQuestionUse.push({
        romanji: "",
        text_jp: "",
        text_kanji: "",
        text_vi: "",
      });
      this.listEdit.push({ edit: 1 });
    },
    loadSetting() {
      this.showHira = localStorage.getItem("showHira") === "true";
      this.typeSelect = localStorage.getItem("typeSelect")
        ? parseInt(localStorage.getItem("typeSelect"))
        : 0;
      this.typeTest = localStorage.getItem("typeTest")
        ? parseInt(localStorage.getItem("typeTest"))
        : 0;
    },
    furiganaRender() {
      if (!this.questionSelect.furigana) return this.questionSelect.text_kanji;

      let text = "";
      this.questionSelect.furigana.map((txt) => {
        if (txt.w !== txt.r) {
          text += "<ruby>";
          text += txt.w;
          text += `<rt>${txt.r}</rt>`;
          text += "</ruby>";
        } else {
          text += txt.w;
        }
      });

      return text;
    },
    updateQuestion(data, noIndex) {
      if (this.loaded) {
        this.listQuestionUse.questions[noIndex] = data;
      }
    },
    async redirectQuiz(mode) {
      const idQuiz = "";

      if (mode === "next") {
        if (this.indexQuizCurrent >= this.quizs.length) return false;

        this.indexQuizCurrent--;
      } else {
        if (this.indexQuizCurrent === 0) return false;

        this.indexQuizCurrent++;
      }

      this.$router.push({
        name: this.$route.name,
        params: { id: this.quizs[this.indexQuizCurrent].id },
      });

      setTimeout(async () => {
        await this.initData();
      }, 100);
    },
    async hideTextJP() {
      this.hideText = !this.hideText;
    },
  },
};
</script>

